import React from 'react';

import styled from 'styled-components';

import {Container, Titulo, Texto} from '../components/GlobalStyles';

import img1 from '../img/isabelParaNiños/1.png';
import img2 from '../img/isabelParaNiños/2.png';
import img3 from '../img/isabelParaNiños/3.png';
import img4 from '../img/isabelParaNiños/4.png';
import img5 from '../img/isabelParaNiños/5.png';
import img6 from '../img/isabelParaNiños/6.png';
import img7 from '../img/isabelParaNiños/7.png';
import img8 from '../img/isabelParaNiños/8.png';
import img9 from '../img/isabelParaNiños/9.png';
import img10 from '../img/isabelParaNiños/10.png';

import AOS from 'aos';
import 'aos/dist/aos.css';
AOS.init();


const listaParaNiños = [
    {
        foto: img1,
        texto: "VINO DE ESPAÑA Y AL LLEGAR A BUENOS AIRES TRABAJO COMO TELEGRAFISTA. CONOCIÓ LA POBREZA Y EL MALTRATO QUE RECIBÍAN LAS MUJERES TRABAJADORAS SE PROPUSO TERMINAR CON ESA OPRESIÓN EN EL NOMBRE DE JESUS."
    },
    {
        foto: img2,
        texto: "TAMBIEN, JUNTO CON SU HERMANA EN LOS RATOS LIBRES VISITABA A LOS ENFERMOS A QUIENES LES LLEVABA PALABRAS DE CONSUELO Y ALEGRÍA."
    },
        {
        foto: img3,
        texto: "Un día mientras Isabel y otras amigas estaban reunidas ella, llena del Espíritu Santo las invitó: ¿Se animarían unirse para salir a anunciar el Evangelio? ¡LA IGLESIA NOS NECESITA!"
    },
        {
        foto: img4,
        texto: "Las jóvenes misioneras bendecidas por Jesús se convirtieron en fervorosas catequistas. Invitaban a todos especialmente a los niños y los preparaban para la Primera Comunión. Ayudaban en sus problemas Muchas familias decían SI a Dios!!!"
    },
        {
        foto: img5,
        texto: "La unión entre Isabel y Jesús Eucaristía era tan viva que ella oraba y soñaba: 'Llevaremos la Buena Noticia a todos los hermanos como lo hizo San Francisco Javier. La Virgen del Carmen será la fundadora. San José nos protegerá al igual que mi querida Santa Teresita'."
    },
        {
        foto: img6,
        texto: "Dios la escuchó y colmó a las hermanitas de sus bendiciones. Comenzaron las obras con la ayuda de personas solidarias. Así se construyó la Capilla Nuestra Señora del Carmen, el colegio para educar cristianamente a muchos niños y un hogar para niñas desamparadas. '¡Dios sea bendito en sus obras!'"
    },
        {
        foto: img7,
        texto: "La gente, especialmente los niños veían a la Madre Isabel siempre sonriente, por eso se acercaban con confianza. Por eso siempre trató de pedir a los padres que dejaran a sus niños acercarse a Jesús a través de la Catequesis. '¡Todo sea para la mayor gloria de Dios!'"
    },
        {
        foto: img8,
        texto: "En las cartas a sus hermanas las Madre Isabel decía: 'vivamos como una familia unida, con humildad y alegría compartiendo todo lo que nos dio Dios. Sirvamos a nuestros hermanos pobres, ellos deben ser los primeros. '¡Hagámoslo con alegría porque poseemos el mayor tesoro que es la gracia de Dios!'"
    },
        {
        foto: img9,
        texto: "La Madre Isabel tenía un sueño misionero: 'y si la Iglesia nos manda lejos, muy lejos donde haya una persona que no conozca a Jesús, allí iremos...' 'Si conociese el mundo la felicidad que se experimenta cuando se trabaja por Dios y por los hermanos... ¡no lo pueden imaginar!'"
    },
        {
        foto: img10,
        texto: "Sierva de Dios Madre Isabel, guía y protege a tu pueblo y haz que tus virtudes sean una guía para todos."
    },
]


const ContainerNiño = styled.div `
    width: 90%;
    display: flex;
    align-items: center;
    flex-direction: column;

    @media (min-width: 768px) {
        flex-direction: row;
        justify-content: space-evenly;

        p {
            max-width: 400px;
        }

        img {
            max-width: 400px;
        }
    }

    img{
        width: 100%;
        margin: 20px 0;
    }
`

const listarNiños = listaParaNiños.map((paraNiños, index)=> {
    return (
        <ContainerNiño key={index}>
            <img src={paraNiños.foto}></img>
            <Texto style={{textIndent: "0", textAlign: "justify", width: "100%"}}>{paraNiños.texto}</Texto>
        </ContainerNiño>
        )
}) 


const ParaNiños = () => {
    return (  
        <Container style={{paddingTop: "120px"}} data-aos="fade-down">
            <Titulo>Madre Isabel para los Niños</Titulo>
            {listarNiños}
        </Container>
    );
}
 
export default ParaNiños;