import React from 'react';
import styled from 'styled-components';
import Actividades from '../components/Actividades';

import Carousel from '../components/Carousel';
import ComponentHome from '../components/ComponentHome'; 
import {Container, Titulo, SubTitulo, Texto} from '../components/GlobalStyles';

import {Link} from 'react-router-dom';

import bg from '../img/backgroundHome2.jpg'

import santo1 from '../img/santos/jesus.jpg'
import santo2 from '../img/santos/sanFranciscoJavier.jpg'
import santo3 from '../img/santos/sanRoque.jpg'
import santo4 from '../img/santos/santaTeresa.jpg'
import santo5 from '../img/santos/devocionesParticulares.jpg'
import santo6 from '../img/santos/santisimaVirgen.jpg'


const ContainerNovedades = styled.div`
    width: 100%;
    background: white;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
`

const ContainerAtividades = styled.div`
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-image: url(${bg});
    background-position: center center;
    background-size: cover;
    background-repeat: no-repeat;
`

const ContainerSantos = styled.div`
    width: 100%;
    height: auto;
    display: flex;
    justify-content: space-evenly;
    align-items: flex-start;
    flex-wrap: wrap;
    margin: 60px 0;
    @media (max-width: 768px) {
        /* flex-direction: column; */
    }
`

const ContainerSanto = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 30%;
    min-width: 320px;
    height: auto;
    margin: 20px 0;
    img {
        width: auto;
        height: 400px;
        @media (max-width: 500px) {
            height: 300px;
        }
    }
    p {
        text-indent: 0;
        text-align: center;
        font-size: 16px;
        margin-top: 20px;
    }
`

const NombreSanto = styled.p`
    font-size: 18px;
    font-weight: 900;
`

const santos = [
    {
        nombre: "Sagrado Corazón de Jesús",
        foto: santo1,
        texto: "El sagrado corazón de Jesús, fuente del amor de Dios del cual debemos ser testigos y portadores en nuestra misión."
    },
    {
        nombre: "San Francisco Javier",
        foto: santo2,
        texto: "Ejemplo de misionero cabal y evangelizador hasta el extremo de dar su vida en esta tarea."
    },
    {
        nombre: "San Roque González",
        foto: santo3,
        texto: " Mártir de la evangelización en nuestro continente y ejemplo de vida y de entrega por la causa de Cristo de su Iglesia."
    },
    {
        nombre: "Santa Teresa del Niño Jesús",
        foto: santo4,
        texto: "Santa Patrona de las misiones y los misioneros, que desde su monasterio de clausura, fue la fuerza de su oración que empujó a los misioneros a predicar y a no claudicar en mi misión."
    },
    {
        nombre: "San José",
        foto: santo5,
        texto: "Primer custodio de la familia de Nazaterth  y misionero del silencio."
    },
    {
        nombre: "La Santísima Virgen bajo la advocación del Carmen",
        foto: santo6,
        texto: "Es la patrona de nuestro instituto y de la capilla de la casa madre lugar donde reposan los restos de nuestra fundadora, la sierva de Dios madre Isabel  Fernández del Carmen."
    },
    
]


const iterarSantos = santos.map((santo, index) => {
    return (
        <ContainerSanto key={index}>
            <NombreSanto>{santo.nombre}</NombreSanto>
            <img src={santo.foto} />
            <Texto>{santo.texto}</Texto>
        </ContainerSanto>
    )
})

const Home = () => {
    return (  
        <Container data-aos="fade-down" >
            {/* <Toolbar /> */}
            <Carousel />
            <Titulo>¿Quiénes somos?</Titulo>
            <SubTitulo style={{textAlign: "center", marginBottom: "100px", marginTop: 0}}>Somos le resultado del carisma otorgado a la Iglesia por nuestra madre fundadora la sierva de Dios Isabel Fernández del Carmen, en elk carisma apostólico misionero para buscar la gloria de Dios y la extensión de su reino tras las huellas dejadas por san Francisco Javier, configurándonos cada vez más con Cristo misionero del Padre, enviado para la salvación del mundo. Contemplativas en acción, capaces de un amor universal, cercanas, sensibles y serviciales en los sufrimientos de los más necesitados en el momento en el cual nos toca vivir. Animadas y confiadas por la vivencia de la frase evangélica <i>“busquen primero el reino de Dios y su justicia y lo demás se les será dado por añadidura”</i></SubTitulo>
            <ContainerNovedades>
                <Titulo style={{marginTop: "20px"}}>
                    <Link to="/iglesiacarmen/novedades" style={{textDecoration: "none", color:"#2e2e2e"}}>
                        Nuestro Patronos
                    </Link>
                </Titulo>
                <SubTitulo>Nuestro Instituto posee sus santos patronos que nos guíen con su ejemplo y nos auxilien en nuestra tarea evangelizadora en el cumplimiento de la espiritualidad dejada por nuestra madre fundadora y que  consagrado al sagrado corazón de Jesús,  debemos ser el ejemplo de su amor en la tierra; como así devociones particulares a los santos misioneros por excelencia;</SubTitulo>
            </ContainerNovedades>
            <ComponentHome />

            <ContainerSantos>
                {iterarSantos}
            </ContainerSantos>
            <ContainerAtividades>
                <Titulo style={{color: 'white'}}>Nuestras Actividades</Titulo>
                <Actividades />
            </ContainerAtividades>

        </Container>
    );
}
 
export default Home;